import logo from './logo.svg';
import './App.css';
import React from "react"
import quotelogo from "./quotes.png"
import Quotecard from "./Quotecard"
class App extends React.Component {
constructor(){
  super()
  this.state={
    quote:"",
    loaded:false,
    image:"",
    imageloaded:false,
    buttonPressed:false,
    person:""

  }
}
componentDidMount(){
  var name= prompt("Please enter your name!")
  while(name===null){
    alert("You need to enter your name")
    name=prompt("Please enter your name!")
  }
  this.setState({person:name})
}

apiCall= async() =>{

  this.setState({buttonPressed:true})
  console.log("inside func")
var image="https://www.picsum.photos/300/300?random=1"
  var randomnumber= Math.floor(Math.random()*1643)
  var randompic=Math.floor(Math.random()*90)
await fetch("https://type.fit/api/quotes").then(res=>res.json()).then(response=>{
  this.setState({quote:response[randomnumber].text})
  this.setState({loaded:true})
  
  alert("Quote loaded. Please scroll down if not visible")
})

await fetch("https://picsum.photos/v2/list?page=2&limit=90").then(data=>data.json()).then(resobj=>{
  this.setState({image:resobj[randompic].download_url})
  this.setState({imageloaded:true})
})
}


  render(){

    var content;
    if(this.state.loaded==false && this.state.imageloaded===false && this.state.buttonPressed===true){
      content=<h4>Loading...</h4>
    }
  return (
    <div className="App">
      <header className="App-header">
      <h1 className="quotesapp">Quotes App</h1>
      <img width="10%"className="quoteslogo"src={quotelogo} alt="Quotes Picture"/>

      <h2 className="clickbtn">Click the button to generate a random, inspirational quote for the day!</h2>
      <div onClick={()=>{this.apiCall()}} className="button_cont" align="center"><a className="example_a" rel="nofollow noopener">Generate Quote</a></div>
      {content}

      {
        this.state.loaded && this.state.imageloaded &&
        (
          <Quotecard quote={this.state.quote} image={this.state.image}/>
        )
      }

    
<br></br>
    <h4 style={{color:"lightsteelblue"}}>{this.state.person} Have a great day!</h4>

<h5 style={{color:"green"}}>Â© Souritra Kar 2020</h5>
<h4 style={{color:"lightsalmon"}}>Check out my Movie Details App :<a  target="_blank"href="https://souritrakar.github.io/movieappfinal/">https://souritrakar.github.io/movieappfinal/</a></h4>
<h5 className="uses">Uses: Lorem Picsum API, Type.Fit API</h5>


      </header>
    </div>
  );
}
}

export default App;
